___CSS_LOADER_EXPORT___.push([module.id, `form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
}

input, select, textarea {
    width: 40vw;
    box-shadow: var(--shadow);
    border: var(--input-border);
    border-radius: 10px;
    font-size: 1rem;
    padding: .5rem 1.5rem;
    accent-color: var(--low-priority);
}

textarea {
    height: 15vh;
    resize: none;
    padding: 1.5rem 1rem;
}


label {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    text-transform: uppercase;
    align-self: flex-start;
    padding: 0rem 1.5rem;
    font-size: .8rem;
    letter-spacing: .3rem;
    font-weight: bold;

    & p {
        opacity: 70%;
        padding: 0rem .5rem;
        margin: 0;
    }
}

input[type=date] {
    padding: .5rem 1.5rem;
    font-size: 1rem;
}

select {
    padding: 1rem;   
    font-size: 1rem;
    background-color: white;
    border-radius: 0px;
}

dialog input textarea select {
    width: 70vw;
    font-size: 1rem;
    padding: 1.5rem;
}

.modal-btns {
    display: flex;

    & button {
        width: 10rem;
    }
}

.close-modal {
    text-align: right;
    padding: 0rem 1rem;
    cursor: pointer;
    & img {
        width: 2rem;
    }
}

.close-modal:hover {
    transition: ease-in-out 400ms;
    opacity: 60%;
}

dialog {
    border: none;
    border-radius: 20px;
    box-shadow: var(--shadow);
    color: var(--text-color);
}

dialog button {
    display: flex;
    align-items: center;
    gap: 1rem;
    justify-content: center;
    color: var(--text-color);
}

.btn-icon {
    width: 1.2rem;
    opacity: 80%;
}
