@font-face {
    font-family: 'Montserrat';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

:root {
    --shadow: rgba(29, 25, 25, 0.16) 0px 1px 4px;
    --light-white: #FBFBFB;
    --light-grey: #F5EDED;
    --text-color: #313d40;

    --low-priority: #b28ae4;
    --medium-priority: #ffce71;
    --high-priority: #ea5b70;
}

*,
*::before,
*::after {

    box-sizing: border-box;
    font-family: inherit; 
}

body {
    display: flex;
    margin: 0;
    font-family: 'Roboto', sans-serif;  
    font-weight: 300;
    font-size: 1.5rem;
    background-color: var(--light-white);
    color: var(--text-color);
}

.app-title {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    min-width: 200px;

    & img {
        width: 100px;
    }
}

nav {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 20%;
    padding: 2rem 0rem;
    background-color: var(--light-grey);
    height: 100vh;
}

.nav-btn {
    display: flex;
    align-items: center;
    padding: 1rem 3rem;
    width: 100%;
    gap: .8rem;
}

.nav-icon {
    width: 1.3rem;
    opacity: 80%;
}

.nav-btn:hover {
    background-color: var(--light-white);
    transition: ease-in-out 400ms;
    cursor: pointer;
}

.nav-btns {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.nav-header {
    display: flex;
    align-items: center;
    gap: .5rem;
    text-transform: uppercase;
    font-size: 1rem;
    padding: 1rem 2rem;
    letter-spacing: .2rem;
    font-weight: bold;
}

#new-project-btn {
    width: 1.5rem;
}

#new-project-btn:hover {
    opacity: 60%;
    transition: ease-in-out 400ms;
    cursor: pointer;
}

.project-btn {
    font-size: 1.3rem;
    gap: 1rem;

    & .bullet-point {
        font-size: 2rem;
        opacity: 80%;
    }
}

h1, h2 {
    margin: 0px;
}

main {
    width: 80%;
    padding: 2rem;
    background-color: var(--light-white);
}

button {
    background-color: var(--light-white);
    border: solid 1px rgba(0, 0, 0, 0.371);
    box-shadow: var(--shadow);
    border-radius: 10px;
    padding:.5rem;
    font-size: 1rem;
    letter-spacing: .1rem;
    padding: 1rem;
    margin: .5rem;
}

button:hover {
    transition: ease-in-out 400ms;
    background-color: white;
    cursor: pointer;
}

header {
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem;
}

#create-new-task {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 2rem;
    & img {
        width: 1.5rem;
    }
}

#create-new-task:hover {
    opacity: 60%;
    transition: ease-in-out 400ms;
    cursor: pointer;
}

#content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    gap: 1rem;
}

#page-title {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.hidden {
    visibility: hidden;
}

.visible {
    visibility: visible;
}


.disabled {
    pointer-events: none;
}

.empty-message {
    opacity: 60%;
    font-style: italic;
}
