___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --text-color: #313d40;
}

#tasks-wrapper { 
    display: flex;
    flex-direction: column;
    min-width: 80%;
    gap: 3rem;
    padding-top: 2rem;
    padding-bottom: 2rem;

    & input {
        width: 1rem;
    }
}

.task {
    display: flex;
    flex-direction: column;
    border: black solid;
    align-items: center;
    padding: 1rem;
    border: solid .1rem rgba(0, 0, 0, 0.225);
    border-radius: 10px;
    box-shadow: var(--shadow);
    cursor: pointer;
    position: relative;
    background-color: white;

    & #expand-task {
        width: 1.5rem;
    }

    #edit-task {
        width: 1.3rem;
    }
}

.task-note {
    font-size: 1.2rem;
    padding-top: 1rem;
}

#edit-task {
    width: 1.5rem;
}

.collapsed {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}

.overdue-label {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    width: 1.5rem;
}

.task-info {
    display: flex;
    justify-content:space-around;
    width: 80%;
}

.not-expanded {
    display: none;
}

.expanded {
    display: block;
    padding: 1rem;
}

.actions {
    display: flex;
    align-items: center;
    gap: .5rem;
    justify-self: flex-end;
}

.error-message {
    color: var(--high-priority);
}

.high-priority {
    border-top: solid .3rem var(--high-priority);
}

.medium-priority {
    border-top: solid .3rem var(--medium-priority);
}

.low-priority {
    border-top: solid .3rem var(--low-priority);
}


.checkbox-label {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
  

.checkbox-label input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.task-completed {
    position: relative; 
    opacity: 50%;
}

.task-completed::after {
    content: "";
    position: absolute;
    left: 8%;
    right: 4%;
    top: 50%;
    height: 2px;
    background-color: var(--text-color);
    transform: translateY(-50%);
    width: 0;
    animation: drawLine 0.5s ease-out forwards;
}

@keyframes drawLine {
    from {
        width: 0;
    }
    to {
        width: 90%;
    }
}

.custom-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
}

.checkbox-label:hover input ~ .custom-checkbox {
    background-color: #ccc;   
}
  
.checkbox-label input:checked ~ .custom-checkbox {
    background-color: var(--text-color);
}
  
.custom-checkbox:after {
    content: "";
    position: absolute;
    display: none;
}
  
.checkbox-label input:checked ~ .custom-checkbox:after {
    display: block;
}
  
.checkbox-label .custom-checkbox:after {
    left: 10px;
    top: 6px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}
